import { useEffect } from "react";
import { Box, Center, Container, Flex } from "@chakra-ui/react";

import {
  CallToAction,
  CoreFeatures,
  FeaturesDisplay,
  Testimonials,
} from "./components";
import Aos from "aos";
import { WavyBox } from "app/components";

export const Home = () => {
  useEffect(() => {
    Aos.init({ duration: 3000 });
  }, []);

  return (
    <Flex flexDir="column" align="center" justify="center" w="100%" minH="100%">
      <Center w="70%" h="90vh">
        <CallToAction />
      </Center>

      <WavyBox direction="up" />
      <Center w="100%" h="90vh" bg="#414558">
        <Box w="70%" h="100%" bg="#414558" data-aos="fade">
          <FeaturesDisplay />
        </Box>
      </Center>
      <WavyBox direction="down" />

      <Center w="100%" h="90vh">
        <Box w="70%" h="100%" data-aos="fade">
          <CoreFeatures />
        </Box>
      </Center>

      <WavyBox direction="up" />
      <Center w="100%" h="90vh" bg="#414558">
        <Box w="70%" h="100%" bg="#414558" data-aos="fade">
          <Testimonials />
        </Box>
      </Center>
      <WavyBox direction="down" />
    </Flex>
  );
};
