import {
  Box,
  Flex,
  Heading,
  Stack,
  Text,
  useColorModeValue,
} from "@chakra-ui/react";
import { WavyBox } from "..";

import { Copyright } from "./Copyright";
import { SocialMediaLinks } from "./SocialMediaLinks";

export const Footer = () => {
  return (
    <>
      <Box
        as="footer"
        role="contentinfo"
        mx="auto"
        maxW="7xl"
        py="12"
        px={{ base: "4", md: "8" }}
      >
        <Stack>
          <Stack
            direction="row"
            spacing="4"
            align="center"
            justify="space-between"
          >
            <Flex flexDir="column" justify="center" align="center" w="20%">
              <Heading textAlign="center" size="md">
                Synapsis Circle
              </Heading>
              <Text textAlign="center" fontWeight="thin">
                Get into the innovative circle
              </Text>
            </Flex>
            <SocialMediaLinks />
          </Stack>
          <Copyright alignSelf={{ base: "center", sm: "start" }} />
        </Stack>
      </Box>
    </>
  );
};
