import { Flex, Icon, Switch, useColorMode } from "@chakra-ui/react";
import { FaMoon } from "react-icons/fa";

export const ColorModeSlider = (props: any) => {
  const { toggleColorMode } = useColorMode();

  return (
    <Flex flexDir="row" justify="center" align="center" {...props}>
      <Icon as={FaMoon} mr={2} />
      <Switch colorScheme="black" onChange={toggleColorMode} />
    </Flex>
  );
};
