import {
  Center,
  Flex,
  Link,
  Button,
  Drawer,
  DrawerBody,
  DrawerCloseButton,
  DrawerContent,
  DrawerOverlay,
  Heading,
  Text,
  Icon,
  useDisclosure,
} from "@chakra-ui/react";
import { Link as RouterLink } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { AiOutlineMenu } from "react-icons/ai";

import { LanguageSlider } from "./LanguageSlider";
import { ColorModeSlider } from "./ColorModeSlider";
import { navLinks } from "./NavLinks";

export const NavMenuSmall = () => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const { t } = useTranslation();

  return (
    <Center borderBottom="1px solid" w="100%" py={4}>
      <Flex flexDir="row" align="center" justify="space-between" w="80%">
        <Flex flexDir="column" justify="center" align="center" w="20%">
          <Heading textAlign="center" size="md">
            Synapsis Circle
          </Heading>
        </Flex>
        <Flex flexDir="row" align="center">
          <Button variant="outline" onClick={onOpen}>
            <Icon as={AiOutlineMenu} />
          </Button>
          <Drawer isOpen={isOpen} placement="left" onClose={onClose}>
            <DrawerOverlay />
            <DrawerContent>
              <DrawerCloseButton />
              <DrawerBody>
                <Flex flexDir="column" justify="center" align="center" py={5}>
                  <Flex
                    flexDir="column"
                    justify="center"
                    align="center"
                    w="100%"
                    borderBottom="1px solid"
                  >
                    <Heading textAlign="center" size="md">
                      Synapsis Circle
                    </Heading>
                    <Text textAlign="center" fontWeight="thin">
                      Get into the innovative circle
                    </Text>
                  </Flex>
                  {navLinks.map((v, i) => (
                    <Link key={i} color="" as={RouterLink} to={v.path} mt={3}>
                      {t(v.translationKey)}
                    </Link>
                  ))}
                </Flex>
                <Flex flexDir="column" align="center" justify="center">
                  <ColorModeSlider mb={3} />
                  <LanguageSlider />
                </Flex>
              </DrawerBody>
            </DrawerContent>
          </Drawer>
        </Flex>
      </Flex>
    </Center>
  );
};
