import { Center, Text, Flex, Heading, Link } from "@chakra-ui/react";
import { useTranslation } from "react-i18next";
import { Link as RouterLink } from "react-router-dom";

import { LanguageSlider } from "./LanguageSlider";
import { navLinks } from "./NavLinks";
import { ColorModeSlider } from "./ColorModeSlider";

export const NavMenuBig = () => {
  const { t } = useTranslation();

  return (
    <Center borderBottom="1px solid" w="100%" h="10%" py={4}>
      <Flex flexDir="row" align="center" justify="space-between" w="70%">
        <Flex flexDir="column" justify="center" align="center" w="20%">
          <Heading textAlign="center" size="md">
            Synapsis Circle
          </Heading>
          <Text textAlign="center" fontWeight="thin">
            Get into the innovative circle
          </Text>
        </Flex>
        <Flex flexDir="row" align="center">
          {navLinks.map((v, i) => (
            <Link key={i} as={RouterLink} to={v.path} w="20%" mr={10}>
              {t(v.translationKey)}
            </Link>
          ))}
          {/* Light or dark. */}
          <ColorModeSlider w="20%" mr={10} />
          <LanguageSlider w="20%" />
        </Flex>
      </Flex>
    </Center>
  );
};
